import styled from "styled-components";
import ICONS from "../../constants/icons";

export const BranchesContainer = styled.div`
  width: 100%;
  height: 100vh;
  padding-top: 72px;
  display: flex;
  flex-wrap: wrap;
`;

export const BranchCell = styled.div`
  width: 25%;
  min-width: 296px;
  flex-grow: 1;
  height: Calc(50vh - 36px);
  display: flex;
  background: ${({ bg, theme }) => (bg ? theme.color.primary : "white")};
  color: ${({ bg, theme }) => (!bg ? theme.color.primary : "white")};
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-family: Mtavruli;
  font-size: 32px;
  padding: 40px 24px;
  cursor: pointer;
  &::after {
    content: "";
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: 0;
  }
  &:hover {
    &::after {
      opacity: 0.1;
    }
  }
`;
