import styled from "styled-components";
import ICONS from "../../constants/icons";

export const HeaderContainer = styled.div`
  width: 100%;
  height: 72px;
  display: flex;
  align-items: center;
  background: ${({ theme }) => theme.color.primary};
  padding: 0 0 0 24px;
  position: fixed;
  z-index: 11111;
  box-shadow: 0px 24px 34px -12px #1b95f88c;
  justify-content: space-between;
  ${(props) => props.theme.tablet`
    padding: 0 16px;
`}
`;

export const HeaderLogo = styled.div`
  min-width: 72px;
  flex-grow: 1;
  height: 72px;
  background-image: url(${ICONS.Logo});
  background-position: left;
  background-size: auto 60%;
  font-family: Gotham-r;
  font-size: 28px;
  color: #e1e6e9;
  padding-left: 60px;
  line-height: 70px;
  ${(props) => props.theme.tablet`
    background-position: right;
    padding-left: 0px;
    padding-right: 36px;
    text-align: right;
    background-size: auto 30px;
    font-size: 24px;
`}
`;

export const HeaderNav = styled.div`
  display: flex;
  align-items: center;
  ${(props) => props.theme.tablet`
  height: 100vh;
  position: fixed;
  width: 48%;
  max-width: 272px;
  background: white;
  top: 0;
  left: 0;
  color: #ffffff;
  background: ${({ theme }) => theme.color.primary};
  padding-top: 80px;
  flex-direction: column;
  display: ${({ menuOpen }) => (menuOpen ? "block" : "none")};
`}
  & a {
    ${(props) => props.theme.tablet`
    width: 100%;
  `}
  }
`;

export const HeaderNavMobileBG = styled.div`
  height: 100vh;
  display: fixed;
  position: absolute;
  width: 100%;
  background: #00000022;
  top: 0;
  left: 0;
`;

export const HeaderNavMobileOpener = styled.div`
  height: 100%;
  width: 20px;
  background-image: url(${ICONS.MobileMenu});
  background-size: 100% auto;
  display: none;
  ${(props) => props.theme.tablet`
  display: block;
`}
`;

export const HeaderNavLink = styled.div`
  padding: 12px;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 72px;
  position: relative;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  &:hover {
    background: #ffffff22;
  }
  &::after {
    content: "";
    display: ${({ active }) => (active ? "block" : "none")};
    position: absolute;
    width: 100%;
    height: 4px;
    border-radius: 2px;
    background: #ffffff;
    bottom: 0;
    box-shadow: 0px 4px 32px 12px #ffffff88;
    left: 0;
    ${(props) => props.theme.tablet`
    width: 90%;
    left: 5%;
  `}
  }
`;
