import styled from "styled-components";
import ICONS from "../../constants/icons";

export const TheoryContainer = styled.div`
  width: 100%;
  height: 100vh;
  padding-top: 72px;
  display: flex;
  flex-wrap: wrap;
`;

export const TheoryCell = styled.div`
  width: 50%;
  min-width: 696px;
  max-width: 100%;
  flex-grow: 1;
  height: Calc(100vh - 72px);
  display: flex;
  background-image: url(${({ image }) => image});
  background-position: ${({ rightImage }) => (rightImage ? "right" : "left")};
  color: ${({ bg, theme }) => (!bg ? theme.color.primary : "white")};
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  font-family: Mtavruli;
  font-size: 65px;
  padding: 48px 72px;
  cursor: pointer;
  ${(props) => props.theme.tablet`
  background-position: center;
    min-width: 100%;
    padding-left: 40px;
  `}
  &::after {
    content: "";
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: 0;
  }
  &:hover {
    &::after {
      opacity: 0.04;
    }
  }
`;
