import styled from "styled-components";
import ICONS from "../../constants/icons";

export const NewsContainer = styled.div`
  width: 100%;
  height: 100vh;
  padding: 108px 8px 32px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`;

export const ScrollableContainer = styled.div`
  width: 100%;
  height: 100%;
  /* width */
  &::-webkit-scrollbar {
    width: 4px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: ${({ theme }) => theme.color.neutral};
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: ${({ theme }) => theme.color.primary};
    border-radius: 2px;
  }
  overflow-y: auto;
  padding: 0 24px;
  ${(props) => props.theme.tablet`
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;padding: 0 20px
  `}
`;
export const NewsColumnTitle = styled.div`
  padding: 16px;
  border-radius: 0 0 4px 4px;
  background: #ffffff;
  position: absolute;
  color: #060000bb;
  font-weight: 600;
  font-size: 18px;
  top: 0;
  left: 24px;
`;

export const NewsLastAdded = styled.div`
  width: Calc(60% - 16px);
  min-width: 320px;
  max-width: 100%;
  flex-grow: 1;
  padding: 78px 0 12px;
  background: ${({ theme }) => theme.color.neutral};
  border-radius: 6px;
  position: relative;
  height: Calc(100vh - 140px);
  margin: 0 8px 40px;
`;

export const NewsPopular = styled.div`
  height: Calc(100vh - 140px);
  min-width: 320px;
  max-width: 100%;
  flex-grow: 1;
  position: relative;
  width: Calc(40% - 16px);
  padding: 78px 0 12px;
  background: ${({ theme }) => theme.color.neutral};
  border-radius: 6px;
  margin: 0 8px 40px;
`;
